<template>
  <section class="home">
    <!-- Button trigger modal -->
    <button type="button" class="btn btn-primary" @click="openModal()">創建工單</button>
    <Table ref="table" />
    <CreateModel ref="openModal" :product="product" />
  </section>
</template>

<script>
// @ is an alias to /src
import CreateModel from '@/components/CreateModel.vue';
import Table from '@/components/TableModel.vue';

export default {
  data() {
    return {
      isNew: false,
      product: {},
    };
  },
  name: 'HomeView',
  components: {
    CreateModel,
    Table,
  },
  methods: {
    checkAuth() {
      const token = document.cookie.replace(
        /(?:(?:^|.*;\s*)testToken\s*=\s*([^;]*).*$)|^.*$/,
        '$1',
      );
      if (token) {
        this.$http.defaults.headers.common.Authorization = `${token}`;
        const api = 'https://ec-course-api.hexschool.io/v2/api/user/check';
        this.$http.post(api, { api_token: this.token }).then((res) => {
          if (res.data.success) {
            this.$refs.table.getData();
          }
        });
      } else {
        alert('尚未登入');
        this.$router.push('./login');
      }
    },
    openModal(isNew, item) {
      this.isNew = isNew;
      if (this.isNew) {
        this.product = {
          imagesUrl: [],
        };
      } else {
        this.product = { ...item };
      }
      this.$refs.openModal.showModal();
    },
  },
  mounted() {
    this.checkAuth();
  },
};
</script>
